import { Chats, Folder, GithubLogo, House, LinkedinLogo, RocketLaunch, UserCircle } from "phosphor-react";
import { Container } from "./styles";

import { Projects } from "../../Projects";
import { useState, createContext, useContext } from "react";
import { Skills } from "../../Skills";
import { MyThemeContext } from "../../../hooks/useThemeApi";


export function Home() {
   const { myTheme, selectMyTheme } = useContext(MyThemeContext)

   const [sumary, setSumary] = useState('');

   function handleSetTheme(id: string, InfoSelect: string) {
      setSumary(InfoSelect)

      selectMyTheme(
         id
      )
   }

   return(
      <div>
      <Container theme={myTheme}>
         <div className="sideBar">
            <button
               type="button"
               onClick={()=>handleSetTheme('cian', 'house')}
            >
               <House size={26} weight="fill"/>
            </button>

            <button
               type="button"
               onClick={()=>handleSetTheme('purple', 'skills')}
            >
               <RocketLaunch size={26} weight="fill"/>
            </button>

            <button
               type="button"
               onClick={()=>handleSetTheme('red', 'projects')}
            >
               <Folder size={26} weight="fill"/>
            </button>

            <button
               type="button"
               onClick={()=>setSumary('sobre')}
            >
               <UserCircle size={26} weight="fill"/>
            </button>

            <button
               type="button"
               onClick={()=>handleSetTheme('yellow', 'contato')}
            >
               <Chats size={26} weight="fill"/>
            </button>
         </div>

         <div className="welcome">           
            <div className="conteudo">
               <h2>Ola, Bem vindo ao meu protflio</h2>
               <h1>Eu sou Gabriel Possas, Dev. Front-End</h1>

               <h4>Tenho 18 anos, sou de Campo Grande-MS, estudo progamao h 1 ano e estou extremamente empolgado em desevenvolver cada vez mais! </h4>

               <div className="icons">
                  <a href="https://www.linkedin.com/in/gabriel-borges-p/" target='_blank'><LinkedinLogo size={32} weight="fill" color="#ffffff" /></a>
                  <a href="https://github.com/Gabrielpossasb" target='_blank'><GithubLogo size={32} weight="fill" color="#ffffff" /></a>
               </div>
            </div>  <div className="dotted">
            </div>
         </div>
      </Container>
     {
      (sumary==='projects')?<Projects/>:<div/> 
     }
     {
      (sumary==='home')?<div/>:<div/> 
     }
     {
      (sumary==='skills')?<Skills />:<div/> 
     }
     
      </div>
   );
}